code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #212121;
	padding-top: 50px;
	color: white;
	text-align: center;
}

.Cell {
	height: 100px;
	width: 100px;
	border-radius: 1px;
	background-color: #263238;
	-webkit-transition: background-color 0.7s ease;
	transition: background-color 0.7s ease;
}

.Cell-lit {
	background-color: #00bcd4;
}

.Board {
	margin: 0 auto;
	box-shadow: 0 0 50px #fff, -10px 0 80px #f0f, 10px 0 80px #0ff;
}

@font-face {
	font-family: 'Big Shoulders Text', cursive;
	src: url('https://fonts.googleapis.com/css?family=Big+Shoulders+Text&display=swap');
}

.Board-title {
	display: flex;
	align-items: center;
	justify-content: center;
}

.container {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

.neon-orange {
	font-family: 'Big Shoulders Text', cursive;
	color: #fb4264;
	font-size: 80px;
	text-shadow: 0 0 3vw #f40a35;
	animation: neon-orange 1s ease infinite;
	-moz-animation: neon-orange 1s ease infinite;
	-webkit-animation: neon-orange 1s ease infinite;
}

.neon-blue {
	font-family: 'Big Shoulders Text', cursive;
	color: #426dfb;
	font-size: 80px;
	text-shadow: 0 0 3vw #2356ff;
	animation: neon-blue 2s linear infinite;
	-moz-animation: neon-blue 2s linear infinite;
	-webkit-animation: neon-blue 2s linear infinite;
	-o-animation: neon-blue 2s linear infinite;
}

.winning-msg .neon-orange,
.winning-msg .neon-blue {
	font-size: 200px;
}

@-webkit-keyframes neon-orange {
	0%,
	100% {
		text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16, 0 0 .4vw #fed128,
			.5vw .5vw .1vw #806914;
		color: #fed128;
	}
	50% {
		text-shadow: 0 0 .5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 .2vw #800e0b,
			.5vw .5vw .1vw #40340a;
		color: #806914;
	}
}

@keyframes neon-orange {
	0%,
	100% {
		text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16, 0 0 10vw #fa1c16, 0 0 .4vw #fed128,
			.5vw .5vw .1vw #806914;
		color: #fed128;
	}
	50% {
		text-shadow: 0 0 .5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b, 0 0 5vw #800e0b, 0 0 .2vw #800e0b,
			.5vw .5vw .1vw #40340a;
		color: #806914;
	}
}

@-webkit-keyframes neon-blue {
	0%,
	100% {
		text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 .4vw #8bfdfe,
			.5vw .5vw .1vw #147280;
		color: #28d7fe;
	}
	50% {
		text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180,
			.5vw .5vw .1vw #0a3940;
		color: #146c80;
	}
}

@keyframes neon-blue {
	0%,
	100% {
		text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff, 0 0 10vw #1041ff, 0 0 .4vw #8bfdfe,
			.5vw .5vw .1vw #147280;
		color: #28d7fe;
	}
	50% {
		text-shadow: 0 0 .5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180, 0 0 5vw #082180, 0 0 .2vw #082180,
			.5vw .5vw .1vw #0a3940;
		color: #146c80;
	}
}

.App {
	text-align: center;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
}

